<template>
  <header>
    <div class="header">
      <div class="header_text">
        <span class="title">회사소식</span>
      </div>
      <div class="header_nav">
        <ul class="list_box">
          <li
            v-for="(category, index) in categories"
            :key="index"
            :class="['list', { active: menuNum === index }]"
            @click="clickNum(index)"
            ref="menuItems"
          >
            {{ category }}
          </li>
        </ul>
      </div>
    </div>
  </header>
</template>

<script>
import { mapState, mapActions } from "vuex";

export default {
  computed: {
    ...mapState(["menuNum", "categories"]),
    // 처음 페이지 로드 시 선택된 메뉴로 자동 스크롤
  },
  mounted() {
    console.log(this.$refs.menuItems[this.menuNum]);
    this.$refs.menuItems[this.menuNum].scrollIntoView({
      behavior: "smooth", // 부드럽게 스크롤
      block: "nearest", // 수직 위치는 유지하고 수평으로만 이동
      inline: "center", // 가로로 중앙에 위치하도록
    });
  },
  methods: {
    ...mapActions(["setClickedNum"]),
    clickNum(index) {
      this.setClickedNum(index);

      const clickedElement = event.target;
      clickedElement.scrollIntoView({
        behavior: "smooth", // 부드럽게 스크롤
        block: "nearest", // 수직 위치는 유지하고 수평으로만 이동
        inline: "center", // 가로로 중앙에 위치하도록
      });
    },
  },
};
</script>

<style src="@/components/sub/menu_renewalNews/header/Header.css" scoped></style>
